<script>
import Vue from 'vue'
import VueGapi from 'vue-gapi'
import VueCharts from 'vue-chartjs'
import axios from 'axios'
import VueAxios from 'vue-axios'

import DailyBreakdownBar from './components/DailyBreakdownBar.vue'
import HourlyBreakdownHeatmap from './components/HourlyBreakdownHeatmap.vue'
import Signin from './components/views/Signin.vue'
import Wait from './components/views/Wait.vue'
import PremiumUpsell from './components/views/PremiumUpsell.vue'
import PremiumUpgrade from './components/views/PremiumUpgrade.vue'
import DateRange from './components/DateRange.vue'
import DateRangeCustom from './components/DateRangeCustom.vue'
import ActivityCards from './components/ActivityCards.vue'
import { dtPreviousWeek, dtPreviousMonth, kFormat } from './utils/utils'

  console.table( VueCharts )

  let doRevr = ( sIn = "tacocat" ) => ( sIn.split("").reverse().join("") );
  Vue.use(VueGapi, {
    apiKey: doRevr("wySB8UJSJTTR2d_k_uN_KWLdDtsYZutUDySazIA"),
    clientId: doRevr("moc.tnetnocresuelgoog.sppa.2n5jn3qn53b76h5sqc92ae7v02qjpep3-277572845182"),
    discoveryDocs: ['https://www.googleapis.com/discovery/v1/apis/gmail/v1/rest'],
    scope: 'https://www.googleapis.com/auth/gmail.readonly',
  })

  Vue.use(VueAxios, axios)

  export default {
    components: {
      Signin,
      Wait,
      PremiumUpsell,
      PremiumUpgrade,
      DailyBreakdownBar,
      HourlyBreakdownHeatmap,
      DateRange,
      DateRangeCustom,
      ActivityCards
    },
    data: () => ({
isSignedIn: false, // (1) Track authenticated state
      activePage: "Signin",
      stateApp: {
        "current": "FREE",
        "state": ["FREE", "PREMIUMUPSELL", "PREMIUMUPGRADE", "PAYCARD", "PAYPAL", "PREMIUM"]
      },
      oAuthToken: "",
      stateRange: {
"current": "Weekly", 
        "Monthly": [0, 0],
        "Weekly": [0, 0],
        "Custom": [0, 0],
      },
      //  Email Message Model
      aMsgsRange: [],
      aMsgsSummary: [], //  {"MessageID":"xx", "DeliveredTo":"qq", "Date":"qq", "From":"qq", "To":"qq", "Subject":"qq"}]
      oMsgsActivity: {"sent": 0, "recipients": 0, "received": 22000, "senders": 33000}, 
      //  Table and Drawer
      drawer: true,
      tablExpanded: [],
      tablSingleExpand: false,
      aMsgsSummaryHeaders: [  //  See Payload > Headers
        {width: "200px", value:"From", text:"From", sortable:true},
        {width: "240px", value:"Date", text:"Date"},
        {width: "240px", value:"To", text:"To"},
        {width: "400px", value:"Subject", text:"Subject"}  //  {value:"DeliveredTo", text:"Recipient"}, 
      ],
      //  Tabs
      tabState: null,
      tabItems: ["Activity","Daily Breakdown","Hourly Breakdown","Ave Response Time","Senders"],
      tabCurrent: "Activity",
      // Graphs
      brandColors: ["#BF8722","#F5771D","#DD4326","#F51D5C","#D91CEB"],
      datacollection: {
        labels: ["Monday","Tuesday","Wednesday","Thursday","Friday","Saturday","Sunday"],
        datasets: [
          {
            label: 'Sent',
            data: [15,22,26,16,7, 0, 0],
            borderColor: "#F5771D",
            backgroundColor: "#F5771D",
          },
          {
            label: 'Recieved',
            data: [18,20,22,24,9,1,2],
            borderColor: "#DD4326",
            backgroundColor: "#DD4326",
          }
        ]
      }
    }),
    provide() {
      return {
        getStateRange: () => this.stateRange,
        getMsgsRange: () => this.aMsgsRange,
        getMsgsSummary: () => this.aMsgsSummary,
        getMsgsActivity: () => this.oMsgsActivity
      }
    },
    watch: {
      isSignedIn: function(val) {
        if(val){
          this.doActionClick("fetchinit")
        }else{
          this.doActionClick("signout")
        }
      },
      stateApp: {
        deep: true,
        handler(val){
          if( val.current === "PREMIUMUPGRADE") this.activePage = "PremiumUpgrade"
          if( val.current === "FREE") this.activePage = null
        }
      }
    },
    methods: {
      tabContent( id ) {
        this.tabCurrent = id
      },
      goHome() {
      },
      signin() {
        this.$gapi.login().then(({ currentUser, hasGrantedScopes }) => {
          console.log("currentUser, hasGrantedScopes", currentUser, hasGrantedScopes )
        })
      },
      signout() {
        this.$gapi.logout()
      },
      toggleTheme(){
        this.$vuetify.theme.dark = !this.$vuetify.theme.dark
        document.body.dataset.prefScheme = (this.$vuetify.theme.dark) ? "dark" : "light" // tsk
      },
      getAuthAccessToken(){
        let goglClient = this.$gapi.clientProvider.client;
        var user = goglClient.authInstance.currentUser.get();
        this.oAuthToken = user.getAuthResponse().access_token;
      },
      fetchGmailMessages( pageToken ){
        let self = this
        let aEpochs = this.stateRange[this.stateRange.current]  //  Current dates
        let sURI = 'https://gmail.googleapis.com/gmail/v1/users/me/messages?q=in: after:' + aEpochs[0] + ' before:' +  + aEpochs[1]
        if( pageToken ) sURI = sURI + `&pageToken=${ pageToken }`
        axios.get( sURI,
        { headers: { 'Authorization': 'Bearer ' + this.oAuthToken } } )
        .then(function(response){
          let oRsp = response.data
console.table( oRsp )
          self.aMsgsRange = self.aMsgsRange.concat( oRsp.messages )
          if( self.aMsgsRange.length > 0 ){
            if(oRsp.nextPageToken){
              self.fetchGmailMessages( oRsp.nextPageToken )
            }else{
              self.aMsgsRange.forEach( (msg, index)=>{
                let exists = self.aMsgsSummary.filter(function(m){ return ( m.id === msg.id ) })
                if( !exists.length ) self.fetchMailDetails( msg.id, self.oAuthToken, index);
              })
            }
          }else{
            console.warn("no msg data")
          }
        })
      },
      fetchMailDetails(id, oAuthToken, index){
        let self = this
        let sURI = 'https://gmail.googleapis.com/gmail/v1/users/me/messages/' + id
        axios.get( sURI,
        { headers: { 'Authorization': 'Bearer ' + this.oAuthToken } } )
        .then(function( response ){
          let emailDetHeaders = response.data.payload.headers
          self.aMsgsSummary.push({
            "id": id,
            "MessageID": self.extractHedr(emailDetHeaders, "Message-ID"),
            "Snippet": response.data.snippet,
            "DeliveredTo": self.extractHedr(emailDetHeaders, "Delivered-To"),
            "Epoch": new Date(self.extractHedr(emailDetHeaders, "Date")).getTime() / 1000,
            "Date": self.extractHedr(emailDetHeaders, "Date"),
            "From": self.extractHedr(emailDetHeaders, "From"),
            "To": self.extractHedr(emailDetHeaders, "To"),
            "Subject": self.extractHedr(emailDetHeaders, "Subject"),
            "labelIds": response.data.labelIds,
          })
          self.populateActivity()
console.table( self.aMsgsSummary[self.aMsgsSummary.length - 1] )
        })
      },
      extractHedr(aHead, sName){
        let sVal = aHead.filter((v)=> (v.name===sName))
        return (sVal[0] && sVal[0].value) ? sVal[0].value : ""
      },
      populateActivity(){
        const aSenders = []
        const aRecipients = []
        this.oMsgsActivity.sent = this.aMsgsSummary.filter( function(mss){
          if(mss.labelIds.find( (ms) => ms === "SENT" )){
            aRecipients.push( mss.To )
            return true
          }
        }).length
        this.oMsgsActivity.recipients = [...new Set( aRecipients )].length
        this.oMsgsActivity.received = (this.aMsgsSummary.length - this.oMsgsActivity.sent)
        this.aMsgsSummary.forEach( (mss)=>{
          aSenders.push( mss.From )
        })
        this.oMsgsActivity.senders = [...new Set( aSenders )].length
      },
      doActionClick( action ){
console.warn( "|> " + action )
        switch(action) {
          case "signin":
            this.signin()
            this.activePage = "Wait"
            break;
          case "clearmsg":
            this.aMsgsRange =  []  //  Email Message Model
            this.aMsgsSummary =  []
            break;
          case "fetchinit":
            this.doActionClick("clearmsg")
            this.getAuthAccessToken()
            this.initDateRanges()
            this.fetchGmailMessages()
            this.populateActivity()
            break;
          case "signout":
            this.signout()
            this.activePage="Signin"
            this.oAuthToken = ""
            this.doActionClick("clearmsg")
            break;
          case "premiumupsell":
            if(this.stateApp.current !== "PREMIUM") this.$set(this.stateApp, 'current', "PREMIUMUPSELL") 
            break;
          case "premiumupgrade":
            if(this.stateApp.current !== "PREMIUM") this.$set(this.stateApp, 'current', "PREMIUMUPGRADE") 
            break;
          case "Monthly":
            this.stateRange.current = action
            this.doActionClick("fetchinit")
            break;
          case "Weekly":
            this.stateRange.current = action
            this.doActionClick("fetchinit")
            break;
          case "Custom": 
            this.activePage = "DateRangeCustom"
            break;
          case "premium-upgrade-close":
            this.$set(this.stateApp, 'current', "FREE") 
            break;
          case "toggle-drawer":
            this.drawer = !this.drawer  //  Not used
            break;
        }
      },
      initDateRanges(){
        this.$set( this.stateRange, "Monthly", dtPreviousMonth( Date.now() ))
        this.$set( this.stateRange, "Weekly",  dtPreviousWeek( Date.now() ))
        if( this.stateRange.Custom[0] === 0 ){  //  Run once
          this.$set( this.stateRange, "Custom",  dtPreviousWeek( Date.now() ))
        }
      }
    },
    mounted(){

    },
    created(){  //  (2) Subscribe to authentication status changes
      this.$gapi.listenUserSignIn((isSignedIn) => {
        this.isSignedIn = isSignedIn
      })
    },
  }
</script>

<template>
  <v-app id="inspire">
    <v-system-bar app height="48" lights-out color="alternative">

      <v-sheet color="primary" class="pr-8 pl-7 pt-0" height="48" >
        <v-card-title class="lgo-text text-center justify-center font-weight-light"
        @click="goHome">Email <span class="font-weight-bold px-1"> Reply </span> Time</v-card-title>
      </v-sheet>

      <v-spacer></v-spacer>
      <v-icon v-if="this.$vuetify.theme.dark"  @click="toggleTheme">mdi-brightness-4</v-icon>
      <v-icon v-if="!this.$vuetify.theme.dark" @click="toggleTheme">mdi-brightness-5</v-icon>
    </v-system-bar>

    <v-navigation-drawer
      v-model="drawer"
      top="48"
      app
    >
      <v-list>
        <v-list-item @click="doActionClick('premiumupgrade')"
        :disabled="!isSignedIn || stateApp.current !== 'FREE'"> 
          <v-list-item-icon>
            <v-icon>mdi-rocket-outline</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>Premium Upgrade</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-list-item v-if="isSignedIn" @click="doActionClick('signout')">
          <v-list-item-icon>
            <v-icon>mdi-logout</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>Sign Out</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

    </v-navigation-drawer>

    <v-main>

      <component
      :is="activePage"
      :is-signed-in = "isSignedIn" 
      @ev-signin="doActionClick( 'signin' )"
      @premium-upgrade-action="doActionClick"
      ></component>

      <v-container
        v-if="isSignedIn"
        class="py-8 px-6"
        fluid
      >
        <v-row>
          <v-col cols="12">
            <date-range
              @state-range-set-current="doActionClick"
            >
            </date-range>
          </v-col>
        </v-row>
        <v-row>

          <v-col cols="12">
            <activity-cards :oMsgsActivity = "oMsgsActivity"
            ></activity-cards>
          </v-col>

          <v-col cols="12">
            <v-card>
              <v-card-title class="text-center justify-left">Email Summary</v-card-title>
                <v-data-table
                  dense
                  :headers="aMsgsSummaryHeaders"
                  :items="aMsgsSummary" 
                  :single-expand="tablSingleExpand"
                  :expanded.sync="tablExpanded"
                  :items-per-page="5"
                  item-key="id"
                  show-expand
                  class="elevation-1 overflow-y-auto"
                  max-height="calc(90vh - 520px)"
                >
                  <template v-slot:expanded-item="{ headers, item }">
                    <td :colspan="headers.length" class="pa-2">
                      {{ item.Snippet }}<br>
                      <v-chip
                      v-for="labl in item.labelIds"
                      :key="labl"
                      class="ma-4"
                      outlined pill small
                      >{{ labl.replace("CATEGORY_","") }}</v-chip>
                    </td>
                  </template>
                </v-data-table>
            </v-card>
          </v-col>

          <v-col cols="12">
            <v-card>
              <v-card-title class="text-center justify-left">Email Visualizations</v-card-title>
                <v-tabs
                  v-model="tabState"
                  background-color="transparent"
                  grow
                >
                  <v-tab
                    v-for="tabItem in tabItems"
                    :key="tabItem"
                    @click="tabContent( tabItem )"
                  >
                    {{ tabItem }}
                  </v-tab>
                </v-tabs>

                <v-tabs-items v-model="tabState">
                  <v-tab-item
                    v-for="tabItem in tabItems"
                    :key="tabItem"
                  >
                    <v-card flat
                    v-if="tabCurrent=='Daily Breakdown'">
                      <v-card-text>Daily Breakdown</v-card-text>
                      <v-row>
                        <v-col cols="6">
                          <daily-breakdown-bar v-if="tabCurrent=='Daily Breakdown'" :chartdata="datacollection"></daily-breakdown-bar>
                        </v-col>
                        <v-col cols="6">
                          <hourly-breakdown-heatmap v-if="tabCurrent=='Daily Breakdown'" :chartdata="datacollection"></hourly-breakdown-heatmap>
                        </v-col>
                      </v-row>
                    </v-card>
                  </v-tab-item>
                </v-tabs-items>
              </v-card>
          </v-col>

        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

