<script>
  export default {
    inject: [ 'getStateRange', 'getMsgsRange', 'getMsgsSummary', 'getMsgsActivity' ],
    computed: {
      stateRange(){
        return this.getStateRange()
      }
    },
    beforeUpdate(){
      console.warn( ">>> ", this.stateRange )
    },
    methods: {
        setCurrent(timeframe){
            this.$emit("state-range-set-current", timeframe)
        },
        dtFormatShort( nDate ) {  //  Tooltip Dates  //  TODO Move to utils
          let dDate = new Date( nDate * 1000 )
          let year = dDate.getFullYear()
          let month = (1 + dDate.getMonth()).toString()
          let day = dDate.getDate().toString()
          month = month.length > 1 ? month : '0' + month
          day = day.length > 1 ? day : '0' + day
          return month + '/' + day + '/' + year
        }
    }
  }
</script>
<template>
    <article>

      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn class="mr-2 ml-2" :color="(stateRange.current==='Monthly') ? 'alternative' : 'secondary'"
          rounded dark
          v-bind="attrs" v-on="on"
          @click="setCurrent('Monthly')"
          >
          <v-icon left>mdi-calendar-month-outline</v-icon>
          Monthly
          </v-btn>          
        </template>
        <span>{{ dtFormatShort(stateRange.Monthly[0]) }}<br>{{ dtFormatShort(stateRange.Monthly[1]) }}</span>
      </v-tooltip>

      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn class="mr-2 ml-2" :color="(stateRange.current==='Weekly') ? 'alternative' : 'secondary'"
          rounded dark
          v-bind="attrs" v-on="on"
          @click="setCurrent('Weekly')"
          >
          <v-icon left>mdi-calendar-range</v-icon>
          Weekly
          </v-btn>
        </template>
        <span>{{ dtFormatShort(stateRange.Weekly[0]) }}<br>{{ dtFormatShort(stateRange.Weekly[1]) }}</span>
      </v-tooltip>

      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn class="mr-2 ml-2" :color="(stateRange.current==='Custom') ? 'alternative' : 'secondary'"
          rounded dark
          v-bind="attrs" v-on="on"
          @click="setCurrent('Custom')"
          >
          <v-icon left>mdi-pencil</v-icon>
          Custom Dates
          </v-btn>
        </template>
        <span>{{ dtFormatShort(stateRange.Custom[0]) }}<br>{{ dtFormatShort(stateRange.Custom[1]) }}</span>
      </v-tooltip>

    </article>
</template>