export function dtPreviousWeek( dNow ) {
    let f = new Date( dNow )
    let l = new Date( dNow )
    f.setDate(f.getDate() - (f.getDay() + 6) % 7)  //  set to Monday of this week
    f.setDate(f.getDate() - 8)
    l.setDate(f.getDate() + 6)
    f.setHours(0,0,0,0)
    l.setHours(23,59,59,999)
    return [Math.round(f.getTime() / 1000), Math.round(l.getTime() / 1000)]
}

export function dtPreviousMonth( dNow ) {
  let f = new Date( dNow )
  let l = new Date( dNow )
  l.setDate(0); f.setDate(0)  //  set to last day of previous month
  f.setDate(1)  //  set to the first day of that month
  f.setHours(0,0,0,0)
  l.setHours(23,59,59,999)
  return [Math.round(f.getTime() / 1000), Math.round(l.getTime() / 1000)]
}

export function kFormat( num) {
    return Math.abs(num) > 999
        ? Math.sign(num) * (Math.abs(num) / 1000).toFixed(1) + 'k'
        : Math.sign(num) * Math.abs(num)
}

export default {
    dtPreviousWeek,
    dtPreviousMonth,
    kFormat
}